const taskTypes = [
  { id: 'call', name: 'Call', icon: 'üìû', color: 'emerald' },
  { id: 'email', name: 'Email', icon: 'üìß', color: 'blue' },
  { id: 'meeting', name: 'Meeting', icon: 'ü§ù', color: 'purple' },
  { id: 'follow_up', name: 'Follow Up', icon: 'üîÑ', color: 'amber' },
  { id: 'demo', name: 'Demo', icon: 'üé¨', color: 'indigo' },
  { id: 'proposal', name: 'Proposal', icon: 'üìÑ', color: 'cyan' },
  { id: 'other', name: 'Other', icon: 'üìã', color: 'slate' },
]

const priorityConfig = {
  urgent: { label: 'Urgent', color: 'bg-red-100 text-red-700 border-red-200' },
  high: { label: 'High', color: 'bg-orange-100 text-orange-700 border-orange-200' },
  medium: { label: 'Medium', color: 'bg-amber-100 text-amber-700 border-amber-200' },
  low: { label: 'Low', color: 'bg-slate-100 text-slate-700 border-slate-200' },
}

export default function TasksPage() {
  const [tasks, setTasks] = useState<ContactTask[]>(mockTasks)
  const [viewMode, setViewMode] = useState<'list' | 'board'>('list')
  const [filterStatus, setFilterStatus] = useState<string>('')
  const [filterPriority, setFilterPriority] = useState<string>('')
  const [filterType, setFilterType] = useState<string>('')
  const [showNewTaskModal, setShowNewTaskModal] = useState(false)
  const [editingTask, setEditingTask] = useState<ContactTask | null>(null)

  // Filter tasks
  const filteredTasks = tasks.filter(task => {
    if (filterStatus && task.status !== filterStatus) return false
    if (filterPriority && task.priority !== filterPriority) return false
    if (filterType && task.type !== filterType) return false
    return true
  })

  // Group tasks by status for board view
  const tasksByStatus = {
    pending: filteredTasks.filter(t => t.status === 'pending'),
    in_progress: filteredTasks.filter(t => t.status === 'in_progress'),
    completed: filteredTasks.filter(t => t.status === 'completed'),
  }

  // Stats
  const stats = {
    total: tasks.length,
    pending: tasks.filter(t => t.status === 'pending').length,
    inProgress: tasks.filter(t => t.status === 'in_progress').length,
    completed: tasks.filter(t => t.status === 'completed').length,
    overdue: tasks.filter(t => t.status !== 'completed' && new Date(t.dueDate) < new Date()).length,
  }

  // Update task status
  const updateTaskStatus = (taskId: string, newStatus: ContactTask['status']) => {
    setTasks(tasks.map(task => 
      task.id === taskId 
        ? { 
            ...task, 
            status: newStatus,
            completedAt: newStatus === 'completed' ? new Date().toISOString() : undefined
          }
        : task
    ))
  }

  // Delete task
  const deleteTask = (taskId: string) => {
    setTasks(tasks.filter(t => t.id !== taskId))
  }

  // Format date
  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const today = new Date()
    const tomorrow = new Date(today)
    tomorrow.setDate(tomorrow.getDate() + 1)

    if (date.toDateString() === today.toDateString()) return 'Today'
    if (date.toDateString() === tomorrow.toDateString()) return 'Tomorrow'
    
    return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' })
  }

  // Check if overdue
  const isOverdue = (task: ContactTask) => {
    if (task.status === 'completed' || task.status === 'cancelled') return false
    return new Date(task.dueDate) < new Date()
  }

  // Get task type icon
  const getTaskTypeIcon = (type: string) => {
    return taskTypes.find(t => t.id === type)?.icon || 'üìã'
  }

  // Get avatar gradient
  const getAvatarGradient = (name: string) => {
    const gradients = [
      'from-violet-500 to-purple-500',
      'from-cyan-500 to-blue-500',
      'from-emerald-500 to-teal-500',
      'from-amber-500 to-orange-500',
      'from-rose-500 to-pink-500',
    ]
    const index = name.charCodeAt(0) % gradients.length
    return gradients[index]
  }

  // Task Card Component
  const TaskCard = ({ task, showContact = true }: { task: ContactTask, showContact?: boolean }) => (
    <div
      className={`bg-white rounded-xl border ${
        isOverdue(task) ? 'border-red-300 bg-red-50' : 'border-slate-200'
      } p-4 hover:shadow-md transition-all`}
    >
      <div className="flex items-start gap-3">
        <div className="w-10 h-10 rounded-xl bg-slate-100 flex items-center justify-center text-xl flex-shrink-0">
          {getTaskTypeIcon(task.type)}
        </div>
        <div className="flex-1 min-w-0">
          <div className="flex items-start justify-between gap-2">
            <h4 className="font-semibold text-slate-900 truncate">{task.title}</h4>
            <span className={`px-2 py-0.5 text-xs font-semibold rounded-full border flex-shrink-0 ${priorityConfig[task.priority].color}`}>
              {priorityConfig[task.priority].label}
            </span>
          </div>
          
          {task.description && (
            <p className="text-sm text-slate-600 mt-1 line-clamp-2">{task.description}</p>
          )}

          {showContact && (
            <div className="flex items-center gap-2 mt-2">
              <div className={`w-6 h-6 rounded-lg bg-gradient-to-br ${getAvatarGradient(task.contactName)} flex items-center justify-center text-white text-xs font-bold`}>
                {task.contactName.split(' ').map(n => n[0]).join('')}
              </div>
              <span className="text-sm text-slate-600 truncate">{task.contactName}</span>
              {task.contactCompany && (
                <span className="text-xs text-slate-400">‚Ä¢ {task.contactCompany}</span>
              )}
            </div>
          )}

          <div className="flex items-center justify-between mt-3">
            <div className="flex items-center gap-2 text-xs text-slate-500">
              <span className={`flex items-center gap-1 ${isOverdue(task) ? 'text-red-600 font-semibold' : ''}`}>
                <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                </svg>
                {formatDate(task.dueDate)}
                {task.dueTime && ` at ${task.dueTime}`}
              </span>
              <span className="flex items-center gap-1">
                <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
                {task.assignedToName}
              </span>
            </div>
            
            {/* Quick Actions */}
            <div className="flex items-center gap-1">
              {task.status !== 'completed' && (
                <button
                  onClick={() => updateTaskStatus(task.id, 'completed')}
                  className="p-1.5 text-emerald-600 hover:bg-emerald-50 rounded-lg transition-colors"
                  title="Mark as completed"
                >
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                  </svg>
                </button>
              )}
              <button
                onClick={() => setEditingTask(task)}
                className="p-1.5 text-slate-500 hover:bg-slate-100 rounded-lg transition-colors"
                title="Edit task"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                </svg>
              </button>
              <button
                onClick={() => deleteTask(task.id)}
                className="p-1.5 text-red-500 hover:bg-red-50 rounded-lg transition-colors"
                title="Delete task"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                </svg>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )

  return (
    <div className="space-y-6 pb-8">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-slate-200 -mx-6 -mt-6 px-6 pt-6 pb-0 mb-6 rounded-t-2xl">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-violet-600 to-purple-600 bg-clip-text text-transparent">
              Contacts
            </h1>
            <p className="text-slate-600 mt-1">
              Manage all your contact-related tasks and activities
            </p>
          </div>
          <button
            onClick={() => setShowNewTaskModal(true)}
            className="px-5 py-2.5 bg-gradient-to-r from-violet-600 to-purple-600 text-white rounded-xl font-medium hover:from-violet-700 hover:to-purple-700 transition-all shadow-lg shadow-violet-500/30 hover:shadow-xl flex items-center gap-2 justify-center"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
            </svg>
            New Task
          </button>
        </div>
        
        {/* Tab Navigation */}
        <div className="flex items-center gap-1 overflow-x-auto pb-0 -mb-px">
          {CONTACT_TABS.map(tab => (
            <Link
              key={tab.id}
              href={tab.href}
              className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap ${
                tab.id === 'tasks'
                  ? 'border-violet-600 text-violet-600'
                  : 'border-transparent text-slate-500 hover:text-slate-700 hover:border-slate-300'
              }`}
            >
              {tab.label}
            </Link>
          ))}
        </div>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-2 lg:grid-cols-5 gap-4">
        <div className="bg-white rounded-xl border border-slate-200 p-4">
          <p className="text-sm text-slate-600 mb-1">Total Tasks</p>
          <p className="text-2xl font-bold text-slate-900">{stats.total}</p>
        </div>
        <div className="bg-amber-50 rounded-xl border border-amber-200 p-4">
          <p className="text-sm text-amber-700 mb-1">Pending</p>
          <p className="text-2xl font-bold text-amber-900">{stats.pending}</p>
        </div>
        <div className="bg-blue-50 rounded-xl border border-blue-200 p-4">
          <p className="text-sm text-blue-700 mb-1">In Progress</p>
          <p className="text-2xl font-bold text-blue-900">{stats.inProgress}</p>
        </div>
        <div className="bg-emerald-50 rounded-xl border border-emerald-200 p-4">
          <p className="text-sm text-emerald-700 mb-1">Completed</p>
          <p className="text-2xl font-bold text-emerald-900">{stats.completed}</p>
        </div>
        <div className="bg-red-50 rounded-xl border border-red-200 p-4">
          <p className="text-sm text-red-700 mb-1">Overdue</p>
          <p className="text-2xl font-bold text-red-900">{stats.overdue}</p>
        </div>
      </div>

      {/* Filters & View Toggle */}
      <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-4 sm:p-6">
        <div className="flex flex-wrap gap-4 items-center justify-between">
          <div className="flex flex-wrap gap-3">
            <select
              value={filterStatus}
              onChange={(e) => setFilterStatus(e.target.value)}
              className="px-4 py-2 border border-slate-300 rounded-xl text-sm font-medium text-slate-700 focus:outline-none focus:ring-2 focus:ring-violet-500"
            >
              <option value="">All Status</option>
              <option value="pending">Pending</option>
              <option value="in_progress">In Progress</option>
              <option value="completed">Completed</option>
            </select>
            <select
              value={filterPriority}
              onChange={(e) => setFilterPriority(e.target.value)}
              className="px-4 py-2 border border-slate-300 rounded-xl text-sm font-medium text-slate-700 focus:outline-none focus:ring-2 focus:ring-violet-500"
            >
              <option value="">All Priority</option>
              <option value="urgent">Urgent</option>
              <option value="high">High</option>
              <option value="medium">Medium</option>
              <option value="low">Low</option>
            </select>
            <select
              value={filterType}
              onChange={(e) => setFilterType(e.target.value)}
              className="px-4 py-2 border border-slate-300 rounded-xl text-sm font-medium text-slate-700 focus:outline-none focus:ring-2 focus:ring-violet-500"
            >
              <option value="">All Types</option>
              {taskTypes.map(type => (
                <option key={type.id} value={type.id}>{type.icon} {type.name}</option>
              ))}
            </select>
          </div>

          {/* View Toggle */}
          <div className="flex bg-slate-100 rounded-xl p-1">
            <button
              onClick={() => setViewMode('list')}
              className={`px-4 py-2 rounded-lg font-medium text-sm transition-all ${
                viewMode === 'list' ? 'bg-white text-violet-600 shadow-sm' : 'text-slate-600'
              }`}
            >
              List
            </button>
            <button
              onClick={() => setViewMode('board')}
              className={`px-4 py-2 rounded-lg font-medium text-sm transition-all ${
                viewMode === 'board' ? 'bg-white text-violet-600 shadow-sm' : 'text-slate-600'
              }`}
            >
              Board
            </button>
          </div>
        </div>
      </div>

      {/* List View */}
      {viewMode === 'list' && (
        <div className="space-y-3">
          {filteredTasks.length === 0 ? (
            <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-12 text-center">
              <div className="w-16 h-16 bg-slate-100 rounded-2xl flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-slate-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                </svg>
              </div>
              <h3 className="text-lg font-semibold text-slate-900 mb-2">No tasks found</h3>
              <p className="text-slate-600">Create a new task or adjust your filters</p>
            </div>
          ) : (
            filteredTasks.map(task => (
              <TaskCard key={task.id} task={task} />
            ))
          )}
        </div>
      )}

      {/* Board View */}
      {viewMode === 'board' && (
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {/* Pending Column */}
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <h3 className="font-semibold text-slate-900 flex items-center gap-2">
                <span className="w-3 h-3 bg-amber-500 rounded-full"></span>
                Pending
              </h3>
              <span className="text-sm text-slate-500">{tasksByStatus.pending.length}</span>
            </div>
            <div className="space-y-3 min-h-[200px] p-3 bg-slate-50 rounded-xl">
              {tasksByStatus.pending.map(task => (
                <TaskCard key={task.id} task={task} showContact={true} />
              ))}
              {tasksByStatus.pending.length === 0 && (
                <p className="text-center text-sm text-slate-500 py-8">No pending tasks</p>
              )}
            </div>
          </div>

          {/* In Progress Column */}
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <h3 className="font-semibold text-slate-900 flex items-center gap-2">
                <span className="w-3 h-3 bg-blue-500 rounded-full"></span>
                In Progress
              </h3>
              <span className="text-sm text-slate-500">{tasksByStatus.in_progress.length}</span>
            </div>
            <div className="space-y-3 min-h-[200px] p-3 bg-slate-50 rounded-xl">
              {tasksByStatus.in_progress.map(task => (
                <TaskCard key={task.id} task={task} showContact={true} />
              ))}
              {tasksByStatus.in_progress.length === 0 && (
                <p className="text-center text-sm text-slate-500 py-8">No tasks in progress</p>
              )}
            </div>
          </div>

          {/* Completed Column */}
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <h3 className="font-semibold text-slate-900 flex items-center gap-2">
                <span className="w-3 h-3 bg-emerald-500 rounded-full"></span>
                Completed
              </h3>
              <span className="text-sm text-slate-500">{tasksByStatus.completed.length}</span>
            </div>
            <div className="space-y-3 min-h-[200px] p-3 bg-slate-50 rounded-xl">
              {tasksByStatus.completed.map(task => (
                <TaskCard key={task.id} task={task} showContact={true} />
              ))}
              {tasksByStatus.completed.length === 0 && (
                <p className="text-center text-sm text-slate-500 py-8">No completed tasks</p>
              )}
            </div>
          </div>
        </div>
      )}

      {/* New Task Modal */}
      {(showNewTaskModal || editingTask) && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-lg w-full shadow-2xl overflow-hidden max-h-[90vh] overflow-y-auto">
            <div className="bg-gradient-to-r from-violet-600 to-purple-600 px-6 py-4 flex items-center justify-between sticky top-0">
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                  </svg>
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white">
                    {editingTask ? 'Edit Task' : 'New Task'}
                  </h3>
                  <p className="text-sm text-violet-100">
                    {editingTask ? 'Update task details' : 'Create a new contact task'}
                  </p>
                </div>
              </div>
              <button
                onClick={() => {
                  setShowNewTaskModal(false)
                  setEditingTask(null)
                }}
                className="text-white hover:bg-white/20 rounded-lg p-2 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Task Title *</label>
                <input
                  type="text"
                  defaultValue={editingTask?.title || ''}
                  placeholder="Enter task title..."
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Description</label>
                <textarea
                  rows={3}
                  defaultValue={editingTask?.description || ''}
                  placeholder="Add task details..."
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500 focus:border-transparent resize-none"
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">Task Type</label>
                  <select
                    defaultValue={editingTask?.type || 'follow_up'}
                    className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500"
                  >
                    {taskTypes.map(type => (
                      <option key={type.id} value={type.id}>{type.icon} {type.name}</option>
                    ))}
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">Priority</label>
                  <select
                    defaultValue={editingTask?.priority || 'medium'}
                    className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500"
                  >
                    <option value="low">Low</option>
                    <option value="medium">Medium</option>
                    <option value="high">High</option>
                    <option value="urgent">Urgent</option>
                  </select>
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">Due Date *</label>
                  <input
                    type="date"
                    defaultValue={editingTask?.dueDate || ''}
                    className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">Due Time</label>
                  <input
                    type="time"
                    defaultValue={editingTask?.dueTime || ''}
                    className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Contact *</label>
                <select
                  defaultValue={editingTask?.contactId || ''}
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500"
                >
                  <option value="">Select a contact...</option>
                  <option value="cnt-001">Michael Johnson - TechCorp</option>
                  <option value="cnt-002">Sarah Williams - Startup.io</option>
                  <option value="cnt-003">Robert Davis - Enterprise Solutions</option>
                  <option value="cnt-004">Emily Brown - Creative Agency</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Assign To</label>
                <select
                  defaultValue={editingTask?.assignedTo || 'user-001'}
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-violet-500"
                >
                  <option value="user-001">John Smith</option>
                  <option value="user-002">Sarah Johnson</option>
                  <option value="user-003">Mike Chen</option>
                </select>
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  onClick={() => {
                    setShowNewTaskModal(false)
                    setEditingTask(null)
                  }}
                  className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium"
                >
                  Cancel
                </button>
                <button
                  className="flex-1 px-4 py-3 bg-gradient-to-r from-violet-600 to-purple-600 text-white rounded-xl font-medium hover:from-violet-700 hover:to-purple-700 transition-all shadow-lg"
                >
                  {editingTask ? 'Update Task' : 'Create Task'}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
